"use client";

import { useMemo, useState } from "react";

import { AddToProjectSteps } from "@/components/builders/AddToProjectSteps";
import { CodeOutput } from "@/components/builders/CodeOutput";
import { ConfigPanel } from "@/components/builders/ConfigPanel";
import Link from "next/link";

export default function CaptchaBuilderPage() {
  const [theme, setTheme] = useState("dark");
  const [gridSize, setGridSize] = useState(4);

  const snippet = useMemo(
    () => `import { CaptchaModal } from "@ogtrust/web";

export function VerifyButton() {
  const [open, setOpen] = useState(false);

  const onVerified = (payload, score) => {
    // ZK proof + validation flow
    setOpen(false);
  };

  return (
    <>
      <button onClick={() => setOpen(true)}>Verify Access</button>
      <CaptchaModal
        isOpen={open}
        onClose={() => setOpen(false)}
        onVerifiedHuman={onVerified}
        theme="${theme}"
        gridSize={${gridSize}}
      />
    </>
  );
}`,
    [theme, gridSize]
  );

  return (
    <div className="mx-auto max-w-3xl space-y-8">
      <div>
        <Link href="/dashboard" className="text-sm text-white/60 hover:text-white/90">
          ‚Üê Dashboard
        </Link>
        <h1 className="mt-2 text-2xl font-semibold">Captcha Builder</h1>
        <p className="mt-1 text-sm text-white/60">
          Get an importable captcha component with tile puzzle + behavioral model.
        </p>
      </div>

      <ConfigPanel
        title="Configure"
        fields={[
          {
            id: "theme",
            label: "Theme",
            type: "select",
            value: theme,
            onChange: setTheme,
            options: [
              { value: "dark", label: "Dark" },
              { value: "light", label: "Light" },
            ],
          },
          {
            id: "grid",
            label: "Grid size",
            type: "number",
            value: gridSize,
            onChange: setGridSize,
          },
        ]}
      />

      <section>
        <h2 className="mb-3 text-lg font-semibold">Generated component</h2>
        <CodeOutput title="React component" code={snippet} />
      </section>

      <AddToProjectSteps
        steps={[
          { num: 1, title: "Install package", body: "npm install @ogtrust/web" },
          { num: 2, title: "Import and use", body: "Add CaptchaModal to your flow and handle onVerifiedHuman." },
          { num: 3, title: "Wire ZK proof", body: "Use generateProof + submitProofForValidation from lib/zk/prove." },
        ]}
      />
    </div>
  );
}
